* {
    box-sizing: border-box;
  }
  
  .app {
    width: 500px;
    border: 1px solid #ddd;
    margin: 50px auto;
  }
  
  .appleBusket {
    width: 470px;
    margin: 20px auto;
    border-radius: 4px;
    border: 1px solid #ddd;
    position: relative;
  }
  .appleBusket > .title {
    padding: 15px 0;
    text-align: center;
    color: #069;
    font-size: 22px;
    font-weight: bold;
  }
  .appleBusket > .stats {
    width: 100%;
    border-top: 1px dashed #ddd;
    border-bottom: 1px dashed #ddd;
    padding: 10px 0;
  }
  .appleBusket > .stats .section {
    display: inline-block;
    width: 50%;
    padding-left: 8px;
  }
  .appleBusket > .stats .section .head {
    padding: 6px 0px;
    font-size: 16px;
    color: #069;
  }
  .appleBusket > .stats .section .content {
    font-size: 20px;
    font-weight: 200;
  }
  .appleBusket > .stats .section:first-of-type {
    border-right: 1px solid #f0f0f0;
  }
  .appleBusket > .appleList {
    padding: 10px 0;
  }
  .appleBusket > .appleList .empty-tip {
    text-align: center;
    font-size: 16px;
    color: #ccc;
    padding: 20px 0;
  }
  .appleBusket > .btn-div {
    text-align: center;
  }
  .appleBusket > .btn-div button {
    border: none;
    background-color: #096;
    color: #fff;
    font-size: 14px;
    cursor: pointer;
    padding: 13px 50px;
    border-radius: 6px;
    margin: 20px auto;
    outline: none;
  }
  .appleBusket > .btn-div button:hover {
    background-color: #55d98d;
  }
  .appleBusket > .btn-div button.disabled {
    background-color: #95a5a6;
    cursor: default;
    opacity: 0.65;
  }
  
  .appleItem {
    width: 420px;
    margin: 10px auto;
    border-radius: 4px;
    border: 1px solid #ddd;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
  .appleItem .apple {
    flex-grow: 2;
  }
  .appleItem .info {
    flex-grow: 15;
  }
  .appleItem .info .name {
    padding: 6px 0;
    font-size: 20px;
    color: #069;
    font-weight: 500;
  }
  .appleItem .info .weight {
    font-size: 16px;
    font-weight: 200;
  }
  .appleItem .btn-div {
    flex-grow: 2;
  }
  .appleItem .btn-div button {
    border: none;
    background-color: #3498db;
    color: #fff;
    font-size: 14px;
    cursor: pointer;
    padding: 10px 24px;
    border-radius: 6px;
    margin: 18px auto;
    outline: none;
  }
  .appleItem .btn-div button:hover {
    background-color: #5dade2;
  }